.app {
  text-align: center;
  font-family: Arial, sans-serif;
  background-image: url('https://www.solidbackgrounds.com/images/2880x1800/2880x1800-rich-electric-blue-solid-color-background.jpg'); /* Add a background image */
  background-size: cover;
  color: #333;
  min-height: 100vh;
  padding: 20px;
}

h1, h2 {
  color: #fff; /* Make headers white for better contrast */
}

.game-container {
  display: flex;
  align-items: flex-start;
}

.sidebar {
  width: 250px;
  max-height: 80vh;
  overflow-y: auto;
  margin-right: 20px;
  background-color: rgba(255, 255, 255, 0.8); /* Light background for sidebar */
  padding: 10px;
  border-radius: 8px;
}

.sidebar button {
  display: block;
  width: 100%;
  margin: 5px 0;
  padding: 10px;
  font-size: 16px;
  cursor: pointer;
  border: none;
  border-radius: 4px;
  background-color: #007bff;
  color: white;
  transition: background-color 0.3s;
}

.sidebar button:hover {
  background-color: #0056b3;
}

.main {
  flex: 1;
  background-color: rgba(255, 255, 255, 0.8); /* Light background for main content */
  padding: 20px;
  border-radius: 8px;
}

.question {
  margin-bottom: 20px;
}

.custom-input {
  margin: 20px 0;
}

.custom-input input {
  padding: 10px;
  font-size: 16px;
  margin-right: 10px;
}

.query textarea {
  width: 100%;
  height: 100px;
  margin: 20px 0;
  padding: 10px;
  font-size: 16px;
}

.controls button {
  padding: 10px 20px;
  font-size: 16px;
  cursor: pointer;
  margin: 5px;
  border: none;
  border-radius: 4px;
  background-color: #28a745;
  color: white;
  transition: background-color 0.3s;
}

.controls button:hover {
  background-color: #218838;
}

.correct-answer {
  margin-top: 20px;
  color: red;
}

.controls {
  display: flex;
  justify-content: center;
  margin-top: 20px;
}

.hint {
  background-color: #f8f9fa;
  border: 1px solid #ced4da;
  padding: 10px;
  margin-top: 20px;
  border-radius: 4px;
  font-style: italic;
}

.api-key-page {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  background-color: rgba(0, 0, 0, 0.5);
}

.api-key-page h2 {
  color: #fff;
  margin-bottom: 20px;
}

.api-key-page form {
  background-color: rgba(255, 255, 255, 0.9);
  padding: 20px;
  border-radius: 8px;
}

.api-key-page input {
  padding: 10px;
  font-size: 16px;
  margin-right: 10px;
}

.api-key-page button {
  padding: 10px 20px;
  font-size: 16px;
  cursor: pointer;
  border: none;
  border-radius: 4px;
  background-color: #007bff;
  color: white;
  transition: background-color 0.3s;
}

.api-key-page button:hover {
  background-color: #0056b3;
}
